@use "./base/colors" as *;
@use "./base/reset" as *;
@use "./layout/header" as *;
@use "./layout/navbar" as *;
@use "./abstracts/mixins" as *;
@use "./pages/services" as *;
@use "./pages/success" as *;
@use "./pages/process" as *;
@use "./pages/faq" as *;
@use "./pages/quote" as *;
@use "./pages/contact" as *;
@use "./layout/footer" as *;

.section {
	&-padding {
		padding: 15em 0;
	}
	&__box {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		text-align: center;
		font-weight: 600;
		&-heading {
			font-size: 4.8rem;
			font-weight: 700;
		}
		&-text {
			font-size: 3.6rem;
			font-weight: 600;
		}
		&-span {
			color: $main-color;
		}
	}
}

@media (min-width: 576px) {
	.header {
		padding: 20em 0 0 0;

		&__right {
			display: flex;
		}
	}
}

@media (min-width: 768px) {
	.header {
		background-image: url("../../dist/img/bgc.svg");

		&__content {
			flex-direction: row;
		}
		&__hero {
			margin-left: -25rem;
			margin-bottom: -2rem;
			max-height: 700px;
			margin-right: 0;
		}
		&__left {
			margin-right: 18em;
		}

		&__left,
		&__right {
			width: 50%;
			height: 100%;
		}
	}
}

@media (min-width: 992px) {
	.navbar {
		background-color: transparent;

		&__links {
			display: flex;
		}
		.burger {
			display: none;
		}
	}
}

@media (min-width: 1200px) {
	.success {
		&__numbers {
			grid-template-columns: repeat(3, 1fr);
		}
	}
}

@media (min-width: 1400px) {
}
