@use "../abstracts/mixins" as *;
@use "../base/colors" as *;

.header {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 15em 0 0 0;
	width: 100%;
	background-color: #1d1d1d;
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	overflow: hidden;
	max-height: 1200px;
	&__content {
		display: flex;
		flex-direction: column;
	}
	// &__hero {
	// 	position: absolute;
	// 	left: 0;
	// 	bottom: 0;
	// 	height: 1000px;
	// }
	&__hero {
		max-height: 300px;
		// max-height: 700px;
		margin-bottom: -4em;
		object-fit: contain;
		margin-right: -20rem;
		// margin-right: 0;
	}
	&__right {
		display: none;
		position: relative;
		align-items: flex-end;
		// & img {
		// 	height: 50%;
		// }
		// &::after {
		// 	position: absolute;
		// 	content: "";
		// 	bottom: 0;
		// 	left: 0;
		// 	background-repeat: no-repeat;
		// 	background-size: contain;
		// 	height: 100%;
		// 	width: auto;
		// 	background-image: url("../../../dist/img/postac1.svg");
		// }
	}
	
	&__left {
		background-color: #1d1d1d;
		color: #fff;
		display: flex;
		flex-direction: column;
		align-self: center;
		gap: 1em;
		&-text {
			font-size: clamp(2.8rem, 2rem + 2.2222vw, 4rem);
			font-weight: 600;
		}
		& span {
			font-size: clamp(1.2rem, 0.4rem + 2.2222vw, 2.4rem);
			font-weight: 300;
		}
		&-description {
			font-size: clamp(1.2rem, 0.4rem + 2.2222vw, 2.4rem);
			font-weight: 300px;
		}
		&-heading {
			font-size: clamp(2rem, 1.2rem + 2.2222vw, 3.2rem);
			font-weight: 500;
			color: $main-color;
		}
		&-button {
			padding: 0.6em 2em;
			font-size: 2rem;
			margin-top: 1em;
			color: $main-color;
			border: 2px solid $main-color;
			width: max-content;
			border-radius: 6px;
			transition: background-color 0.3s;
			&:hover {
				background-color: #f2ac202f;
			}
		}
	}
}
